<template>
  <div class="data-extraction-apply">
    <div>
      <div class="topTitle">
        <span class="topTitle-span1">检查数据提取</span>
        <span class="topTitle-span1">
          <img src="../../../assets/topTitle1.png" alt />
        </span>
        <span class="topTitle-span1">提取数据申请</span>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>申请信息
          <div class="stepBox">
            <div class="stepBoxCell" v-for="(item,index) in stepBox" :key="item.nodeName">
              <div :class="item.nodeId>workFlowState?'stepBoxCell-1':'stepBoxCell-1-2'">{{index+1}}</div>
              <div class="stepBoxCell-2">{{item.nodeName}}</div>
              <div class="stepBoxCell-3" v-if="stepBoxCell3(index)"></div>
            </div>
          </div>
        </div>
        <div class="contentCellBody1">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="提取数据期间：" prop="extraTime">
                <DatePicker
                  split-panels
                  type="daterange"
                  placeholder="请选择时间"
                  :value="formValidate.extraTime"
                  @on-change="formValidate.extraTime=$event"
                ></DatePicker>
              </FormItem>
              <FormItem label="提交截止日期：" prop="deadLine">
                <DatePicker
                  type="date"
                  placeholder="请选择时间"
                  :options="options1"
                  :value="formValidate.deadLine"
                  @on-change="formValidate.deadLine=$event"
                ></DatePicker>
              </FormItem>
              <FormItem label="提取机构：" prop="extraUnit" class="specical-tree">
                <treeselect
                  v-model="formValidate.extraUnit"
                  :options="data2"
                  placeholder="请选择机构"
                  style="width:300px"
                />
              </FormItem>
            </Row>
            <Row>
              <FormItem label="权限使用时间：" prop="permissionTime">
                <DatePicker
                  type="daterange"
                  placeholder="请选择时间"
                  :options="options1"
                  :value="formValidate.permissionTime"
                  @on-change="formValidate.permissionTime=$event"
                ></DatePicker>
              </FormItem>
              <FormItem label="紧急程度：" prop="emergeLevel" style="width:315px">
                <RadioGroup v-model="formValidate.emergeLevel">
                  <Radio
                    v-for="item in emergeLevels"
                    :label="item.code"
                    :key="item.code"
                  >{{item.name}}</Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label="流程标题：" prop="bpmTitle">
                <Input v-model="formValidate.bpmTitle" placeholder="请输入主题" style="width:310px"></Input>
              </FormItem>
            </Row>
            <Row>
              <Form-item label="附件：" prop="filePath" class="form-item" style="width:100%">
                <Upload
                  ref="upload"
                  multiple
                  type="drag"
                  :show-upload-list="false"
                  :default-file-list="defaultList"
                  :before-upload="handleUploadfile"
                  :format="['zip','rar','xls','xlsx','doc','docx','pdf']"
                  :max-size="10240"
                  :on-format-error="handleFormatError"
                  :on-exceeded-size="handleMaxSize"
                  :action="axios.defaults.baseURL+'/system/remote/micUpload'"
                  :on-success="handleSuccess"
                  :on-error="handleError"
                >
                  <div style="padding: 20px 0">
                    <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                    <p>请上传附件</p>
                  </div>
                </Upload>
                <div>上传格式为zip、rar、xls、xlsx、doc、docx、pdf，且大小不超过10MB</div>
              </Form-item>
              <Form-item label class="form-item" style="width:100%">
                <Table border :columns="columns1" :data="uploadList"></Table>
              </Form-item>
            </Row>
            <Row>
              <FormItem label="备注：" prop="notes" class="form-item" style="width:100%">
                <Input
                  v-model="formValidate.notes"
                  :autosize="{minRows: 2,maxRows: 5}"
                  type="textarea"
                  style="width:100%;"
                />
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="next('formValidate')">下一步</Button>
              </div>
            </Row>
            <Modal :mask-closable="false" v-model="modal1" width="360" class="treeBoxModel">
              <p slot="header" style="color:#f60;text-align:center">
                <span>审核人信息</span>
              </p>
              <div style="text-align:left;overflow-y:auto;height:300px">
                <!-- <FormItem prop="bpmTitle" :label-width="10">
                <Input
                type="textarea" :autosize="{minRows: 2,maxRows: 5}" 
                  v-model="formValidate.bpmTitle"
                  placeholder="请输入此次审核名称"
                 
                ></Input>
                </FormItem>-->
                <FormItem prop="nextUserInfo">
                  <Tree
                    ref="treeRef"
                    :data="data1"
                    show-checkbox
                    multiple
                    @on-check-change="updateNextUserInfo"
                  ></Tree>
                </FormItem>
              </div>
              <div slot="footer">
                <Button type="primary" size="large" long @click="handleSubmit('formValidate')">立即申请</Button>
              </div>
            </Modal>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td>{{tableHeader.title1}}</td>
                  <td class="textLeft">{{tableHeader.title2}}</td>
                  <td>{{tableHeader.title3}}</td>
                  <td>{{tableHeader.title4}}</td>
                  <td>{{tableHeader.title5}}</td>
                  <td>{{tableHeader.title6}}</td>
                  <td>{{tableHeader.title7}}</td>
                  <!-- <td class="textLeft">{{tableHeader.title8}}</td> -->
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{index+1}}</td>
                  <td class="textLeft">{{item.name}}</td>
                  <td>{{item.success}}</td>
                  <td>{{item.error}}</td>
                  <td>{{item.status}}</td>
                  <td>{{item.startTime}}</td>
                  <td>{{item.endTime}}</td>
                  <!-- <td class="textLeft"></td> -->
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect },
  props: ["applytop"],
  created() {
    this.getsecondshow();
    console.log(this.applytop);
    let that = this;
    this.axios
      .get("/extract/getExtractDic/EMERCRGENCY_LEVEL")
      .then(function(response) {
        if (response.code == "200") {
          that.emergeLevels = response.data.EMERCRGENCY_LEVEL;
        }
      })
      .catch(function(error) {});
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.data2 = response.data;
          if (that.formValidate.extraUnit == null)
            that.formValidate.extraUnit = that.data2[0].id;
          that.selectTreeCleanUp(that.data2);
        }
      })
      .catch(error => {});
  },
  beforeUpdate() {
    this.uploadList = this.$refs.upload.fileList;
  },
  data() {
    const validaterFile = (rule, value, callback) => {
      if (this.uploadList.length == 0) {
        return callback(new Error("请上传附件"));
      } else {
        callback();
      }
    };
    const validaterExtraTime = (rule, value, callback) => {
      if (this.formValidate.extraTime[0] == "") {
        return callback(new Error("请选择时间"));
      } else {
        callback();
      }
    };
    const validaterExtraUnit = (rule, value, callback) => {
      if (this.formValidate.extraUnit == undefined) {
        return callback(new Error("请选择提取机构"));
      } else {
        callback();
      }
    };
    const validaterPermissionTime = (rule, value, callback) => {
      if (this.formValidate.permissionTime[0] == "") {
        return callback(new Error("请选择时间"));
      } else {
        callback();
      }
    };
    const validaterNextUserInfo = (rule, value, callback) => {
      if (this.formValidate.nextUserInfo == "" && this.modal1) {
        return callback(new Error("请至少选择一个审核人"));
      } else {
        callback();
      }
    };
    //   const validaterNextStepName = (rule, value, callback) => {
    //   if (this.formValidate.bpmTitle == "" && this.modal1) {
    //     return callback(new Error("请输入名称"));
    //   } else {
    //     callback();
    //   }
    // };
    return {
      options1: {
        disabledDate(date) {
          return date && date.valueOf() < Date.now() - 24 * 60 * 60 * 1000;
        }
      },
      defaultList: [],
      modal1: false,
      data2: [],
      data1: [
        // {
        //   title: "反洗钱部门专员",
        //   code: "AmlDirector_Dept",
        //   expand: true,
        //   children: [
        //     {
        //       title: "名称",
        //       code: "lihl2",
        //       expand: true
        //     },
        //     {
        //       title: "名称",
        //       code: "lihl1",
        //       expand: true
        //     }
        //   ]
        // }
      ],
      columns1: [
        {
          title: "文件名",
          key: "name"
        },
        {
          title: "操作",
          key: "",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.fileDownload(params.index, params.row.name);
                    }
                  }
                },
                "下载"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.remove(params.index);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ],
      stepBox: [],
      workFlowState: "01",
      tableHeader: {
        title1: "序",
        title2: "检查表名称",
        title3: "成功数据量",
        title4: "错误数据量",
        title5: "执行状态",
        title6: "执行开始时间",
        title7: "执行结束时间",
        title8: "操作"
      },
      tableBody: [
        {
          name: "机构对照表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "业务类型对照表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "存量个人客户身份信息表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "存量单位客户身份信息表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "受益所有人身份信息表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "产品户信息表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "资金账户表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "证券账户表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "结算资金流水表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "单客户多银行资金调拨流水表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "证券交割表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "特定业务办理明细表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "存量客户当前风险等级表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "存量客户检查期限内历次风险等级划分表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "大额交易报告明细表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        },
        {
          name: "可疑交易报告明细表",
          success: "",
          error: "",
          startTime: "",
          endTime: "",
          flag: false
        }
      ],
      uploadList: [],
      formValidate: {
        fileName: [],
        nextUserInfo: "",
        filePath: [],
        extraTime: ["", ""], //提取数据时间
        extraUnit: null, //机构
        permissionTime: ["", ""], //期限时间
        emergeLevel: "", //紧急
        extraApplicant: "lihl",
        deadLine: "", //提交截止日期
        notes: "", //备注
        bpmTitle: ""
      },
      emergeLevels: [],
      ruleValidate: {
        //    bpmTitle: [
        //   {
        //     required: true,
        //     validator: validaterNextStepName,
        //     trigger: "on-blur"
        //   },
        //    {
        //     type: "string",
        //     max: 1000,
        //     message: "您超出了范围",
        //     trigger: "blur"
        //   },
        // ],
        filePath: [
          {
            required: true,
            validator: validaterFile,
            trigger: "change"
          }
        ],
        bpmTitle: [
          {
            required: true,
            message: "请输入主题",
            trigger: "blur"
          },
          {
            type: "string",
            max: 50,
            message: "最大输入长度不能超过50字",
            trigger: "blur"
          }
        ],
        nextUserInfo: [
          {
            required: false,
            validator: validaterNextUserInfo,
            trigger: "on-check-change"
          }
        ],
        notes: [
          {
            type: "string",
            max: 128,
            message: "长度最大不能超过128",
            trigger: "blur"
          }
        ],
        extraUnit: [
          {
            required: true,
            message: "请选择机构",
            validator: validaterExtraUnit,
            trigger: "xxxx"
          }
        ],
        emergeLevel: [
          { required: true, message: "请选择紧急程度", trigger: "change" }
        ],
        extraTime: [
          {
            required: true,
            message: "请选择时间",
            validator: validaterExtraTime,
            trigger: "xxxx"
          }
        ],
        deadLine: [
          {
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime: [
          {
            required: true,
            validator: validaterPermissionTime,
            message: "请选择时间",
            trigger: "xxxx"
          }
        ]
      }
    };
  },
  watch: {
    "formValidate.extraTime": function(newValue, oldValue) {
      this.$refs["formValidate"].validateField("extraTime");
    },
    "formValidate.permissionTime": function(newValue, oldValue) {
      this.$refs["formValidate"].validateField("permissionTime");
    },
    "formValidate.extraUnit": function(newValue, oldValue) {
      this.$refs["formValidate"].validateField("extraUnit");
    }
  },
  methods: {
    getsecondshow(){
      let that = this;
      let obj = this.applytop;
      this.axios
        .post(`/extract/pageReviewDetail?bathcId=${obj}&Id=1`)
        .then(response => {
          if (response.code == "200") {
            let data0 = response.data.basicData;
            let data1 = response.data.recordDatas;
            data0.bpmTitle = data0.taskName;
            that.formValidate = data0;
            let url = `/bpm/getNodeDefDetailByPerson?username=${data0.extraApplicant}&bpmType=extract`;
            // ===========
            for (let i = 0; i < that.formValidate.fileList.length; i++) {
              that.defaultList.push({
                url: that.formValidate.fileList[i].fileId,
                name: that.formValidate.fileList[i].filename
              });
            }
            this.axios
              .get(url)
              .then(function(response) {
                that.stepBox = response.data;
                if (response.data.length == 0) {
                  that.$Message.warning({
                    content: "当前你无权申请",
                    duration: 3
                  });
                } else {
                  let data = {
                    bpmName: response.data[1].bpmName,
                    nodeId: response.data[1].nodeId,
                    roleName: response.data[1].roleName
                  };
                  that.formValidate.bpmName = response.data[1].bpmName;
                  data = JSON.stringify(data);
                  that.axios
                    .post("/bpm/getNextRole", data, {
                      headers: { "Content-Type": "application/json" }
                    })
                    .then(response => {
                      if (response.code == "200") {
                        that.data1 = response.data;
                        that.treeNLeafNodeDisable(that.data1);
                      }
                    })
                    .catch(error => {});
                }
              })
              .catch(function(error) {});
            // ============
            that.workFlowState = data0.workFlowState;
            // that.formValidate.bpmTitle = that.formValidate.taskName;
            if (
              that.formValidate.fileName != null &&
              that.formValidate.fileName != ""
            ) {
              that.formValidate.fileName = JSON.parse(
                that.formValidate.fileName
              );
              that.formValidate.filePath = JSON.parse(
                that.formValidate.filePath
              );
            } else {
              that.formValidate.fileName = [];
              that.formValidate.filePath = [];
            }
            that.formValidate.extraTime = new Array(
              that.formValidate.extraTime,
              that.formValidate.extraTime2
            );
            that.formValidate.permissionTime = new Array(
              that.formValidate.permissionTime,
              that.formValidate.permissionTime2
            );
          }
        });
    },
    getFormatDate(num) {
      var date = new Date(new Date() + num * 24 * 60 * 60 * 1000);
      var seperator1 = "-";
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate;
      return currentdate;
    },
    handleFormatError(file) {
      this.$Notice.warning({
        title: "附件格式错误",
        desc: "请选择zip、rar、xls、xlsx、doc、docx、pdf."
      });
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: "超出了附件大小限制",
        desc: " 不应超过10M"
      });
    },
    updateNextUserInfo(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidate.nextUserInfo = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.formValidate.nextUserInfo = element.code;
      });
      this.$refs["formValidate"].validateField("nextUserInfo");
    },
    //禁用非叶子节点
    treeNLeafNodeDisable(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children) {
          this.$set(treeDatas[i], "disabled", true);
          this.$set(treeDatas[i], "expand", true);
          this.treeNLeafNodeDisable(treeDatas[i].children);
        }
      }
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    remove(index) {
      let filePath = this.uploadList[index].url;
      this.axios.post(`/system/remote/delFile/${filePath}`).then(response => {
        if ((response = true)) {
          this.uploadList.splice(index, 1);
          this.$Message.success("删除成功");
        } else {
          this.$Message.error("删除失败");
        }
      });
    },
    handleUploadfile(file) {
      const check = this.uploadList.length < 5;
      if (!check) {
        this.$Notice.warning({
          title: "只能上传最多五个附件."
        });
      }
      return check;
    },
    handleSuccess(response, file) {
      if (response.code == "200") {
        file.url = response.data[0].fileId;
        this.$refs["formValidate"].validateField("filePath");
      }
    },
    fileDownload(index, fileName) {
      let that = this;
      let filePath = this.uploadList[index].url;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    handleError() {
      this.$Message.error("上传失败");
    },
    stepBoxCell3(index) {
      let index2 = index + 1;
      if (index2 == this.stepBox.length) {
        return false;
      } else {
        return true;
      }
    },
    next(name) {
      this.$refs[name].validate(valid => {
        if (valid) this.modal1 = true;
        else this.$Message.error("校验失败!");
      });
    },
    handleSubmit(name) {
      let that = this;
      this.$refs[name].validate(valid => {
        if (valid) {
          that.uploadList.forEach(element => {
            that.formValidate.fileName.push(element.name);
            that.formValidate.filePath.push(element.url);
          });
          let data = JSON.parse(JSON.stringify(that.formValidate));
          data.fileName = JSON.stringify(data.fileName);
          data.filePath = JSON.stringify(data.filePath);
          data.extraTime = JSON.stringify(data.extraTime);
          data.permissionTime = JSON.stringify(data.permissionTime);
          data.taskName=JSON.stringify(data.bpmTitle);
          data = JSON.stringify(data);
          // debugger
          this.axios
            .post("/extract/task/doRejectSubmitRetry", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                 this.$emit("close");
              } else {
                that.$Message.error(response.message);
              }
            })
            .catch(error => {});
        }
      });
    }
  }
};
</script>
<style scoped>
.treeBoxModel >>> .ivu-form-item-content {
  margin-left: 0px !important;
}
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
</style>


