<template>
  <div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>申请信息
        <div class="stepBox">
          <div class="stepBoxCell" v-for="(item,index) in stepBox" :key="item.nodeName">
            <div :class="item.nodeId>workFlowState?'stepBoxCell-1':'stepBoxCell-1-2'">{{index+1}}</div>
            <div class="stepBoxCell-2">{{item.nodeName}}</div>
            <div class="stepBoxCell-3" v-if="stepBoxCell3(index)"></div>
          </div>
        </div>
      </div>
      <div class="contentCellBody1">
        <Form ref="formValidate" :label-width="120" inline>
          <Row>
            <FormItem label="提取数据期间：" prop="extraTime">
              <label style="font-size:14px">{{formValidate.extraTime[0].slice(0,10)}}<span v-if="formValidate.extraTime[0]">~</span>{{formValidate.extraTime[1].slice(0,10)}}</label>
            </FormItem>

            <FormItem label="提取机构：" prop="extraUnit">
              <treeselect
                disabled
                v-model="formValidate.extraUnit"
                :options="data2"
                placeholder="请选择提取机构："
                style="width:200px"
              />
            </FormItem>
            <FormItem label="提交截止日期：" prop="deadLine">
              <label style="font-size:14px">{{formValidate.deadLine.slice(0,10)}}</label>
            </FormItem>
          </Row>
          <Row>
            <FormItem label="权限使用时间：" prop="permissionTime">
              <label style="font-size:14px">{{formValidate.permissionTime[0].slice(0,10)}}<span v-if="formValidate.permissionTime[0]">~</span>{{formValidate.permissionTime[1].slice(0,10)}}</label>
            </FormItem>
            <FormItem label="紧急程度：" prop="emergeLevel">
              <label style="font-size:14px" v-if="emergeLevels[formValidate.emergeLevel-1]">{{emergeLevels[formValidate.emergeLevel-1].name}}</label>
            </FormItem>
            <FormItem label="流程标题："  prop= "bpmTitle">
                <Input v-model="formValidate.bpmTitle"  style="width:170px" :readonly="disabledFlag"></Input>
            </FormItem>
          </Row>
          <Row>
            <FormItem label="备注：" class="form-item" style="width:100%">
              <Input
                v-model="formValidate.notes"
                disabled
                :autosize="{minRows: 2,maxRows: 5}"
                type="textarea"
                style="width:100%;"
              />
            </FormItem>
          </Row>
          <Row>
            <div class="fujian">
              <div class="fujian-title">附件:</div>
              <div v-for="(item,index) in fileList" :key="index">
                <span
                  style="width:100%;height:40px;line-height:20px;overflow:hidden;text-overflow: ellipsis;margin-bottom:5px"
                  class="cursorPointer"
                  @click="fileDownload(item.fileId, item.filename)"
                >{{item.filename}}</span>
              </div>
            </div>
          </Row>
        </Form>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>数据列表
      </div>
      <div class="contentCellBody">
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td>{{tableHeader.title1}}</td>
                <td class="textLeft">{{tableHeader.title2}}</td>
                <td>{{tableHeader.title3}}</td>
                <td>{{tableHeader.title4}}</td>
                <td>{{tableHeader.title5}}</td>
                <td>{{tableHeader.title6}}</td>
                <td>{{tableHeader.title7}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBody" :key="item.id">
                <td>
                  {{index+1}}
                </td>
                <td class="textLeft">{{item.tableName}}</td>
                <td>{{item.execuStatus=='运行结束'?item.succNum:''}}</td>
                <td>{{item.execuStatus=='运行结束'?item.errorNum:''}}</td>
                <td  v-if="item.execuStatus=='运行失败'"  style="color:RGB(250,174,98)">{{item.execuStatus}}</td>
                <td  v-if="item.execuStatus=='运行结束'"  style="color:RGB(56,218,167)">{{item.execuStatus}}</td>
                <td  v-if="item.execuStatus=='运行中'" style="color:RGB(230,230,230)">{{item.execuStatus}}</td>
                <td  v-if="item.execuStatus=='等待'"  style="color:RGB(230,230,230)">{{item.execuStatus}}</td>
                <td>{{item.execuStartTime?item.execuStartTime.substr(0,19):''}}</td>
                <td>{{item.execuEndTime?item.execuEndTime.substr(0,19):''}}</td>
              </tr>
            </tbody>
          </table>
        </Row>
      </div>
    </div>
    <div style="width:100%;height:80px">如执行失败，请点击重新执行按钮，如反复失败请联系运维人员 0123-XXXXXXXX</div>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  props:['dataObj'],
  components: { Treeselect },
  created() {
    let that = this;
    let obj =  this.dataObj;
    this.axios
      .post(`/extract/pageReviewDetail?bathcId=${obj.batchId}&Id=${obj.id}`)
      .then(response => {
        if (response.code == "200") {
          let data0 = response.data.basicData;
          let data1 = response.data.recordDatas;
          data0.bpmTitle = data0.taskName;
          that.formValidate = data0;
          //that.formValidate.bpmTitle = that.formValidate.taskName;
          //that.workFlowState = data0.workFlowState;
          that.fileList = that.formValidate.fileList;
          that.formValidate.extraTime = new Array(
            that.formValidate.extraTime,
            that.formValidate.extraTime2
          );
          that.formValidate.permissionTime = new Array(
            that.formValidate.permissionTime,
            that.formValidate.permissionTime2
          );
          that.tableBody = data1;
          for (let i = 0; i < that.tableBody.length; i++) {
            that.$set(that.tableBody[i], "flag", false);
          }
        }
      });
    this.axios
      .get(
        "/extract/getExtractDic/EMERCRGENCY_LEVEL,BUSI_SCOPE"
      )
      .then(function(response) {
        if (response.code == "200") {
          that.emergeLevels = response.data.EMERCRGENCY_LEVEL;
          that.busiScopes = response.data.BUSI_SCOPE;
        }
      })
      .catch(function(error) {});
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
            that.selectTreeCleanUp(response.data);
            that.data2 = response.data;
        }
      })
      .catch(error => {});
      //暂时注释掉
    // this.axios
    //   .get("/bpm/getNodeDefDetail")
    //   .then(function(response) {
    //     that.stepBox = response;
    //   })
    //   .catch(function(error) {});
  },
  data() {
    return {
      workFlowState: 1,
      fileList: [],
      stepBox: [],
       tableHeader: {
        title1: "序",
        title2: "检查表名称",
        title3: "成功数据量",
        title4: "错误数据量",
        title5: "执行状态",
        title6: "执行开始时间",
        title7: "执行结束时间",
      },
      tableBody: [],
      data2: [],
      formValidate: {
        fileName: [],
        filePath: [],
        nextUserInfo: "",
        extraTime: ["", ""], //提取数据时间
        extraUnit: "", //机构
        permissionTime: ["", ""], //期限时间
        emergeLevel: "", //紧急
        extraApplicant: "lihl",
        deadLine: "", //提交截止日期
        fileType: "", //文件格式
        notes: "" //备注
        // -----
      },
      extraUnits: [],
      busiScopes: [],
      emergeLevels: [],
      statusList: [],
      ruleValidate: {
        filePath: [
          {
            required: true,
            trigger: "change"
          }
        ],
        extraUnit: [
          {
            required: true,
            message: "请选择机构",
            trigger: "change"
          }
        ],
        emergeLevel: [
          { required: true, message: "请选择紧急程度", trigger: "change" }
        ],
        extraTime: [
          {
            required: true,
            message: "请选择时间",
            trigger: "xxxx"
          }
        ],
        deadLine: [
          {
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime: [
          {
            required: true,
            message: "请选择时间",
            trigger: "xxxx"
          }
        ]
      }
    };
  },
  methods: {
    stepBoxCell3(index) {
      let index2 = index + 1;
      if (index2 == this.stepBox.length) {
        return false;
      } else {
        return true;
      }
    },
    selectTreeCleanUp(treeDatas){
        for (let i = 0; i < treeDatas.length; i++) {
          if (treeDatas[i].children && treeDatas[i].children.length >0) {
              this.selectTreeCleanUp(treeDatas[i].children);
          }
          else this.$delete(treeDatas[i], "children");
        }
    },
    fileDownload(filePath, fileName) {
      let that = this;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    applyBtn() {
      this.$router.push({ name: "dataExtractionApply" });
    },
    successfun(item) {
      this.$router.push({
        name: "dataExtractionPreview",
        query: {
          type: "success",
          batchId: item.batchId,
          isSuc: "0",
          reDoNum: item.execuApplicationNew,
          tableName: item.tableField
        }
      });
    },
    errorfun(item) {
      this.$router.push({
        name: "dataExtractionPreview",
        query: {
          type: "error",
          batchId: item.batchId,
          isSuc: "1",
          reDoNum: item.execuApplicationNew,
          tableName: item.tableField
        }
      });
    }
  }
};
</script>
<style scoped>
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.fujian {
  padding-left: 122px;
  position: relative;
}
.fujian-title {
  position: absolute;
  left: 73px;
}
.fujianself {
  width: 250px;
  height: 20px;
  border-radius: 4px;
  padding: 2px 20px;
  background-color: RGB(238, 243, 248);
  cursor: pointer;
}
.operationSpan {
  color: blue;
  margin: 0 5px;
  cursor: pointer;
}
</style>


